@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;700;800;900&display=swap);
.NavElement {
    width: 150px;
    height: 145px;
    position: relative;
    padding: 20px;
}

.NavTopSquare {
    background-color: transparent;
    border-top: 1px solid #FFFFFF;
    border-right: 1px solid #FFFFFF;
    width: 35px;
    height: 35px;
    position: absolute;
    top: 0;
    right: 0;
}

.NavLinks {
    color: #FFFFFF;
    font-weight: 300;
}

.NavLinks li {
    display: flex;
    justify-content: flex-end;
    /* align-items: stretch; */
}

.NavLinks li a {
    text-align: right;
    margin: 0px 30px 20px 0px;
}

.NavLinkText {
    text-decoration: none;
    color: #FFFFFF;
}

.NavLinkTextActive {
    font-weight: bold!important;
}

.NavLinkText, .NavLinkSquare {
    transition: all .15s ease-out;
}

.NavLinkText:hover {
    color: #FF009C;
    font-weight: bold;
    cursor: pointer;
}

.NavLinkText:hover ~ .NavLinkSquare {
    background-color: #FF009C;
    cursor: pointer;
}

.NavLinkSquare {
    width: 15px;
    height: 15px;
    background-color: #121212;
    outline: 2px solid #FFFFFF;
    outline-offset: -2px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.NavBotSquare {
    background-color: transparent;
    border-bottom: 1px solid #FFFFFF;
    border-right: 1px solid #FFFFFF;
    width: 35px;
    height: 35px;
    position: absolute;
    bottom: 12.5px;
    right: 0;
}

.NavFloatingSquare {
    width: 15px;
    height: 15px;
    background-color: transparent;
    border-right: 1px solid #FFFFFF;
    position: absolute;
    top: -45px;
    right: 0;
}

.CurrentActive {
    position: absolute;
    color: #FFFFFF;
    font-weight: 400;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    top: -55px;
    right: -32.5px;
}
.HeaderContainer {
    color: #FFFFFF;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.HeaderContainer span {
    font-weight: bold;
}

.HeaderContainer ul {
    list-style-type: none;
}

.HeaderContainer ul li {
    float: left;
    font-size: 28px;
    margin-left: 25px;
}

.HeaderContainer ul li > .Icon, .HeaderContainer ul li {
    transition: all .2s ease-out;
}

.HeaderContainer ul li > .Icon:hover, .HeaderContainer ul li:hover {
    color: #FF009C;
    cursor: pointer;
}

.FullNameHeader > span {
    color: #FFFFFF;
    transition: all .2s ease-out;
}

.FullNameHeader > span:hover {
    color: #FF009C;
}

.FooterContainer {
    color: #121212;
    width: 100%;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.FooterContainer span {
    font-weight: bold;
}
::-webkit-scrollbar {
    width: 0;
}

#ProgressBarContainer {
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    z-index: 998;
}

#ProgressBar {
    z-index: 998;
    height: 100%;
    width: 10px;
    background: #FF009C;
    -webkit-transform-origin: top right;
            transform-origin: top right;
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
}

#ProgressBar:before {
    z-index: 998;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 0, 156, 0.75);
    -webkit-filter: blur(5px);
            filter: blur(5px);
}

#ProgressBar:after {
    z-index: 998;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 0, 156, 0.5);
    -webkit-filter: blur(10px);
            filter: blur(10px);
}
