:root {
    font-family: 'Montserrat', sans-serif;

    --BG1FontSize: 15vw;
}

.Home, .Projects, .About, .Contact {
    background-color: #121212;
    padding: 0 100px 0 100px;
}

.Content {
    min-height: 100vh;
}

.Home {
    position: relative;
}

.About {
    position: relative;
}

body {
    overflow-x: hidden;
}

* {
    margin: 0;
    padding: 0;
}

p, h1, h2, span {
    cursor: default;
}

.App {
    width: 100%;
    height: 100%;
}

