.Background {
    width: 80%;
    padding: 25% auto auto 10%;
}

.BackgroundText {
    color: #121212;
    text-shadow: 0 0 25px rgba(1, 1, 1, 1);
    font-weight: 800;
    white-space:nowrap;
    padding-top: 75px;
    margin-left: -17.5px;
}

.BG1 {
    font-size: 15vw;
    font-size: var(--BG1FontSize);
}

.BG2 {
    font-size: 15vw;
    font-size: var(--BG1FontSize)
}

.BG3 {
    font-size: calc(15vw - 3vw);
    font-size: calc(var(--BG1FontSize) - 3vw)
}

.BG4 {
    font-size: calc(15vw - 3vw);
    font-size: calc(var(--BG1FontSize) - 3vw)
}

.HomeText {
    color: #FFFFFF;
    font-size: 48px;
    font-weight: 300;
    position: absolute;
    bottom: 250px;
}

.AboutText {
    color: #FFFFFF;
    font-size: 30px;
    font-weight: 300;
    bottom: 250px;
    width: 50%;
    margin-top: 150px;
}

.AboutText p span {
    font-weight: 400;
}

.Nav {
    position: fixed;
    right: 100px;
    bottom: 250px;
    z-index: 999;
}

.FullName {
    font-weight: bold;
}

.FullName > span {
    color: #FFFFFF;
    transition: all .2s ease-out;
}

.FullName > span:hover {
    color: #FF009C;
}

.InitialFirstName {
    color: #FF009C!important;
}

.InitialLastName {
    color: #FF009C!important;
    margin-left: 12.5px;
}

.Footer {
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    z-index: -1;
    background-color: #FFFFFF;
    box-shadow: inset 0 0 15px 0 #121212;
}

.ComingSoon {
    text-align: center;
    font-size: calc(15vw - 12vw);
    font-size: calc(var(--BG1FontSize) - 12vw);
    padding-top: 150px;
    padding-bottom: 150px;
}
